export default {
    name: 'UniRefresh',
    props: {
        display: {
            type: [String],
            default: "hide"
        }
    },
    data() {
        return {
            pulling: false
        }
    },
    computed: {
        isShow() {
            if (this.display === "show" || this.pulling === true) {
                return true;
            }
            return false;
        }
    },
    created() {},
    methods: {
        onchange(value) {
            this.pulling = value;
        },
        onrefresh(e) {
            this.$emit("refresh", e);
        },
        onpullingdown(e) {




            var detail = {
                viewHeight: 90,
                pullingDistance: e.height
            }
            this.$emit("pullingdown", detail);

        }
    }
}
